@import "src/styles/mixins";

.root {
  padding: 30px 15px;
  max-width: 1200px;
  margin: 0 auto;
}

.first-block {
  display: flex;
  height: 100%;
  width: 100%;
  align-items: center;
  justify-content: center;
  margin: 0 auto;
}

.second-block {
  margin-top: 20px;
}

.row {
  align-items: center;
  width: 100%;
  max-width: 1200px;
}

.image {
  display: flex;
  justify-content: center;
}

.info {
  width: 400px;
  max-width: 100%;
  margin: 0 auto !important;

  @include media-max-width(md) {
    width: 100%;
    margin-top: 20px !important;
  }
}

.input-number {
  width: 100% !important;
}

.button {
  width: 100%;
  display: block;
  margin-bottom: 10px;
}
